const btnmenu = document.querySelector(".btn-menu"),
  menulist = document.querySelector(".menu");
let menuNumber = 0;

btnmenu.addEventListener('click', () => {
  menulist.classList.toggle("active");
});




const modal = document.querySelector('.modal'),
  btn = document.querySelector(".btn-get"),
  close = document.querySelector('.close'),
  backform = document.querySelector('.back-form'),
  blackout = document.querySelector('.modal-blackout'),
  modalContent = document.querySelector('.modal-content'),
  successContent = document.querySelector('.success-content');
//вызов модального окна
document.querySelector(".btn-get2").onclick = function () {
  modal.style.display = 'block';
  blackout.style.display = 'block';
  stepFrom.classList.add('active-step');
};
//вызов модального окна
btn.onclick = function () {
  modal.style.display = 'block';
  blackout.style.display = 'block';
  stepFrom.classList.add('active-step');
};

//закрытие модального окна
close.onclick = function () {
  CloseModal();
};
//нажатие по области вне модального окна
blackout.onclick = function () {
  CloseModal();
};
function CloseModal() {
  modal.style.display = 'none';
  blackout.style.display = 'none';
  formStepInfo.classList.remove('active-form');
  formStepDate.classList.remove('active-form');
  formStepSize.classList.remove('active-form');
  formStepWhere.classList.remove('active-form');
  modalContent.classList.remove('unactive');
  successContent.classList.remove('active');
  stepFrom.classList.remove('active-step');
  stepInfo.classList.remove('active-step');
  stepDate.classList.remove('active-step');
  stepSize.classList.remove('active-step');
  stepWhere.classList.remove('active-step');
  backform.classList.remove('active');
  formStepFrom.classList.add('active-form');
}


const btnNextFrom = document.querySelector('.btn-next-step-from'),
  btnNextInfo = document.querySelector('.btn-next-step-info'),
  btnNextDate = document.querySelector('.btn-next-step-date'),
  btnNextSize = document.querySelectorAll('.btn-next-step-size'),
  btnNextWhere = document.querySelector('.btn-next-step-where');

const formStepFrom = document.querySelector('.form-step-from'),
  formStepInfo = document.querySelector('.form-step-info'),
  formStepDate = document.querySelector('.form-step-date'),
  formStepSize = document.querySelector('.form-step-size'),
  formStepWhere = document.querySelector('.form-step-where');

const stepFrom = document.querySelector('.step-from'),
  stepInfo = document.querySelector('.step-info'),
  stepDate = document.querySelector('.step-date'),
  stepSize = document.querySelector('.step-size'),
  stepWhere = document.querySelector('.step-where');

//переход на следующую форму Личной Информации
btnNextFrom.onclick = function () {
  formStepFrom.classList.remove('active-form');
  formStepInfo.classList.add('active-form');
  stepInfo.classList.add('active-step');
  backform.classList.add('active');
  c = 1;
};
//переход на следующую форму Даты
btnNextInfo.onclick = function () {
  formStepInfo.classList.remove('active-form');
  formStepDate.classList.add('active-form');
  stepDate.classList.add('active-step');
  c = 2;
};
//переход на следующую форму Размера
btnNextDate.onclick = function () {
  formStepDate.classList.remove('active-form');
  formStepSize.classList.add('active-form');
  stepSize.classList.add('active-step');
  c = 3;
};
//переход на следующую форму Места

btnNextSize.forEach(function(NextSizeFun) {
  NextSizeFun.addEventListener('click', function() {
    formStepSize.classList.remove('active-form');
    formStepWhere.classList.add('active-form');
    stepWhere.classList.add('active-step');
    c = 4;
  })
});



//переход на следующую форму Успешного заполнения
btnNextWhere.onclick = function () {
  formStepWhere.classList.remove('active-form');
  modalContent.classList.add('unactive');
  successContent.classList.add('active');
  backform.classList.remove('active');
};



let c;
backform.onclick = function () {
  if (c == 1) {
    formStepFrom.classList.add('active-form');
    formStepInfo.classList.remove('active-form');
    stepInfo.classList.remove('active-step');
    backform.classList.remove('active');
  }
  if (c == 2) {
    formStepInfo.classList.add('active-form');
    formStepDate.classList.remove('active-form');
    stepDate.classList.remove('active-step');
    c = 1;
  }
  if (c == 3) {
    formStepDate.classList.add('active-form');
    formStepSize.classList.remove('active-form');
    stepSize.classList.remove('active-step');
    c = 2;
  }
  if (c == 4) {
    formStepSize.classList.add('active-form');
    formStepWhere.classList.remove('active-form');
    stepWhere.classList.remove('active-step');
    c = 3;
  }
}









